import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Nodes from '../Nodes/Nodes';
import Appbar from '../Appbar/Appbar';
import RootOptions from '../Options/RootOptions';
import NodeOptions from '../Options/NodeOptions';

const styles = theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing.unit * 4,
  },
});

class NestedList extends React.Component {
  state = {
    open: false,
  };

  handleClick = (e) => {
    console.log(e.type)
    this.setState(state => ({ open: !state.open }));
  };

  handleRightClick = (e) => {
    console.log(e.type)
    this.setState(state => ({ open: !state.open }));
  };



  render() {
    const { classes } = this.props;

    return (
      <div>
      <Appbar/>
      <List
        component="nav"
        subheader={<ListSubheader component="div">user generated factories</ListSubheader>}
        className={classes.root}
      >



        <ListItem button onClick={this.handleClick} onContextMenu={this.handleRightClick}>

          <ListItemText inset primary="Root" />

          {this.state.open ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={this.state.open} timeout="auto" unmountOnExit>
          <Nodes/>
        </Collapse>
      </List>
      <RootOptions/>
      <NodeOptions/>
      </div>
    );
  }
}

NestedList.propTypes = {
  classes: PropTypes.object.isRequired,
};


export default withStyles(styles)(NestedList);
